$(document).ready(function() {
    sliderMain();
    sliderAbout();
    sliderCertificates();
    sliderNews();
    sliderServices2();


    // Menu
    $('.burger').click(function () {
        $(this).toggleClass('burger--active')
        $('.header-top').toggleClass('header-top--active')
        $('body').toggleClass('fixed-body')
    })

    // Services - menu
    $(".header-top__item").hover(onIn, onOut);
    $(".header-top__item .header-top__item-show").click(function() {
        if ($(this).parents(".header-top__item").hasClass("header-top__item--active")) {
            $(this).parents(".header-top__item").removeClass("header-top__item--active")
        } else {
            $(this).parents(".header-top__item").addClass("header-top__item--active")
        }
    })

    // Filter
    $('.filter-btn').click(function () {
        $('.sidebar').addClass('sidebar--active')
        $('.sidebar-bg').addClass('sidebar-bg--active')
        $('.sidebar-close').addClass('sidebar-close--active')
        $('body').addClass('fixed-body')
    })

    $('.sidebar-close').click(function () {
        $('.sidebar').removeClass('sidebar--active')
        $('.sidebar-bg').removeClass('sidebar-bg--active')
        $('.sidebar-close').removeClass('sidebar-close--active')
        $('body').removeClass('fixed-body')
    })

    // Up
    var btn = $('.up');
    $(window).scroll(function() {
        if ($(window).scrollTop() > 300) {
            btn.addClass('up--active');
        } else {
            btn.removeClass('up--active');
        }
    });
    btn.on('click', function(e) {
        e.preventDefault();
        $('html, body').animate({scrollTop:0}, '300');
    });

    // Tabs
    $(".tab").click(function () {
        let path = $(this).attr("data-tab-path")
        $(this).siblings(".tab").removeClass("tab--active")
        $(this).addClass("tab--active")

        $(this).parents(".tabs").find(".tabs__content").removeClass("tabs__content--active")
        $(this).parents(".tabs").find(`.tabs__content[data-content-path="${path}"]`).addClass("tabs__content--active")
    })

    // Accordion
    $(".show").click((function(){
    $(this).toggleClass("show--active"),
    $(this).siblings(".hidden").slideToggle()}));

})

function onIn() {
    if (window.innerWidth > 740) {
        let el = $(this)
        setTimeout(function() {
            if (el.is(':hover')) {
                console.log(el)
                el.addClass("header-top__item--active")

            }
        }, 200);
    }
}

function onOut() {
    if (window.innerWidth > 740) {
        $(this).removeClass("header-top__item--active")
    }
}